var sourceType = [
		['camera'], //拍照
		['album'], //相册
		['camera', 'album'] //拍照或相册
	]
	var sizeType = [
		['compressed'], //压缩
		['original'], //原图
		['compressed', 'original'] //压缩或原图
	]
export default{
	data(){
		return{
			uid:'',
			token:'',
			pic:[],
			imageList: [], //保存图片路径集合
			imageLength: 5, //限制图片张数
			sourceTypeIndex: 2, //添加方式限制
			sizeTypeIndex: 2, //图片尺寸限制
			// 地址
			lat:'',
			lng:'',
			address:'',
			data:{},
			loaded:false,
		}
	},
	onLoad(){
		var userInfo=uni.getStorageSync('userInfo'),that=this
		if(userInfo!='' & userInfo!=null & userInfo!=undefined){
			this.uid=userInfo.uid
			this.token=userInfo.token
		} 
		this.getDetail()
	},
	// watch:{
	// 	'data'{
	// 		deep:true,
			
	// 	}
	// },
	methods:{
		getDetail(){
			this.$loading()
			var that=this
			var params={
				uid:this.uid,
				token:this.token
			}
			var url='&r=api.myshop.my'
			  this.$apiPost(url,params).then((res) =>{
				  that.pic=[]
					that.data=res.data
					that.imageList=res.data.banner.split(',')
					that.loaded=true
					that.lng=res.data.lng
					that.lat=res.data.lat
					uni.hideLoading()
			  })
		},
		// logo更换
		changeHeadface(){
						var  that = this;
						uni.chooseImage({
						    success: (chooseImageRes) => {
								if(chooseImageRes.tempFiles[0].size > 2097152){
									that.$msg('图片过大，请重新选择图片')
									return ;
								}
								uni.showLoading()
						        const tempFilePaths = chooseImageRes.tempFilePaths[0];
						        that.$upload(tempFilePaths,'',(res) =>{
						        	that.data.logo=res
									uni.hideLoading()
						        	// that.submitPersonalInfo()
						        	})
						    }
						});
		},
		//选择图片
		chooseImage: async function() {
				uni.chooseImage({
					sourceType: sourceType[this.sourceTypeIndex],

					sizeType: sizeType[this.sizeTypeIndex],

					count: this.imageLength - this.imageList.length,
					success: (res) => {
						this.imageList=this.imageList.concat(res.tempFilePaths);
					}
				})
			},
			//预览图片
			previewImage: function(e) {
				var current = e.target.dataset.src
				uni.previewImage({
					current: current,
					urls: this.imageList
				})
			},
			//删除图片
			deleteImage: function(e) {
				if(this.imageList.length<=1){
					return ;
				}
				var index = e.currentTarget.dataset.index;
				var that = this;
				var images = that.imageList;
				images.splice(parseInt(index), 1);
				that.imageList = images;
			},
			chooseLocation(){
				var that=this
				uni.chooseLocation({
					success:(res) =>{
						that.lat=res.latitude
						that.lng=res.longitude
						that.data.address=res.name
					}
				})
			},
			switch1Change(e){
				var v=e.detail.value
				if(v==true){
					this.data.merch_status='6'
				}else{
					this.data.merch_status='2'
				}
			},
			submit(){
				var  that=this,l=this.imageList.length;
				this.$loading()
				if(l==0){
					this.$msg('至少需上传一张图片')
					return ;
				}else{
					this.imageList.forEach((item,index) =>{
						if(item.indexOf('blob')>-1){
							that.$upload(item,'',(cb) =>{
								that.pic.push(cb)
							var	t=this.pic.length
								if(l==t){
									that.submitForm()
								}
							})
						}else{
							that.pic.push(item)
							var	t=this.pic.length
								if(l==t){
									that.submitForm()
								}
						}
					})
				}
				
			},
			submitForm(){
				var that=this
				var params={
					uid:this.uid,
					token:this.token,
					logo:this.data.logo,
					banner:this.pic.join(','),
					merchname:this.data.merchname,
					address:this.data.address,
					lng:this.lng,
					lat:this.lat,
					merch_status:this.data.merch_status
				}
				var url='&r=api.myshop.my.change'
				  this.$apiPost(url,params).then((res) =>{
						that.$msg('修改成功')
						uni.hideLoading()
						uni.$emit('updateOperating')
						setTimeout(function(){
							that.getDetail()
						},500)
				  })
			}
	}
}
